@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow: hidden;
  font-family: "Raleway";
}

.node td {
  border: 1px solid rgb(175, 216, 248);
  width: 25px;
  height: 25px;
}

.node-start {
  background-image: url(/path-visualizer/static/media/angle-right-solid.fbb21171.svg);
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-animation-name: specialNodes;
          animation-name: specialNodes;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.node-finish {
  background-image: url(/path-visualizer/static/media/target.71c472f9.svg);
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-animation-name: specialNodes;
          animation-name: specialNodes;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.shortest-path-node {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.node-wall {
  -webkit-animation-name: wallAnimation;
          animation-name: wallAnimation;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: #97d523;
    border-radius: 100%;
  }

  50% {
    background-color: #3ce7a0;
  }

  100% {
    transform: scale(1);
    background-color: #23d5ab;
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: #97d523;
    border-radius: 100%;
  }

  50% {
    background-color: #3ce7a0;
  }

  100% {
    transform: scale(1);
    background-color: #23d5ab;
  }
}

@-webkit-keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 255, 255);
    background-image: url(/path-visualizer/static/media/angle-right-solid.fbb21171.svg);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 255, 255);
  }

  100% {
    transform: scale(1);
    background-color: rgb(241, 226, 11);
  }
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 255, 255);
    background-image: url(/path-visualizer/static/media/angle-right-solid.fbb21171.svg);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 255, 255);
  }

  100% {
    transform: scale(1);
    background-color: rgb(241, 226, 11);
  }
}

@-webkit-keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(12, 53, 71, 0.8);
  }

  50% {
    transform: scale(1.2);
    background-color: rgba(12, 53, 71, 1);
  }

  100% {
    transform: scale(1);
    background-color: rgba(12, 53, 71, 0.5);
  }
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(12, 53, 71, 0.8);
  }

  50% {
    transform: scale(1.2);
    background-color: rgba(12, 53, 71, 1);
  }

  100% {
    transform: scale(1);
    background-color: rgba(12, 53, 71, 0.5);
  }
}

@-webkit-keyframes specialNodes {
  0% {
    transform: scale(0.3);
  }

  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes specialNodes {
  0% {
    transform: scale(0.3);
  }

  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.navbar {
  background-color: rgb(150, 75, 0);
}
.visualize-button {
  background-image: linear-gradient(
    to right,
    #f6d365 0%,
    #fda085 51%,
    #f6d365 100%
  );
  flex: 0.1 1 auto;
  margin: 5px;
  padding: 5px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  /* box-shadow: 0 0 20px #eee; */
  border-radius: 10px;
}

.visualize-button:hover {
  background-position: right center;
}

.board {
  margin-top: 5%;
  border-collapse: collapse;
}

.board td {
  border: 1px solid black;
  width: 25px;
  height: 25px;
}

